}

.toaster__card .btn-refresh {
  color: #ffeb3b;
  font-size: 1.3em;
  right: 50px;
  width: auto;
}

.toaster__card .icon {
  display: block;
  font-size: 20px;
}

#video {
  position: fixed;
  top: 50%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  z-index: -100;
  transform: translateX(-50%) translateY(-50%);
  pointer-events: none;
}

#image {
  bottom: 0;
  height: 224px;
  opacity: 0;
  position: absolute;
  width: 224px;
  z-index: -10;
}

.snap {
  height: 100vh;
  overflow: hidden;
  position: relative;
}

.snap__instructions {
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  height: 100vh;
  position: relative;
}

.snap__wrap {
  margin: auto;
  text-align: center;
}

.snap__wrap h2, .snap__wrap p {
  color: #ffffff;
  text-shadow: 0 2px 0 #000000;
}

.snap__wrap h2 {
  font-size: 3.6em;
}

.snap__wrap p {
  margin: 0;
}

.snap__wrap svg {
  width: 100px;
}

.snap__canvas {
  height: 100%;
  opacity: 0;
  position: absolute;
  width: 100%;
  z-index: -10;
}

.snap__message {
  color: #ffffff;
  font-weight: 100;
  font-size: 4em;
  line-height: 1;
  padding: 0 20px;
  position: absolute;
  top: 25px;
  text-align: center;
  text-shadow: 0 1px 8px #000000;
  width: 100%;
  z-index: 10;
}

.snap__details {
  bottom: 0;
  color: #ffffff;
  left: 0;
  padding: 0 20px 15px;
  position: fixed;
  text-shadow: 0 1px 1px rgba(0, 0, 0, 0.8);
  width: 100%;
  z-index: 20;
}

.snap__details p {
  line-height: 1.25;
  margin: 0;
}

.snap__viewer {
  transition-property: opacity, transform, visibility;
  transition-duration: 1s;
  transition-timing-function: cubic-bezier(0.19, 1, 0.22, 1);
  background-color: #000000;
  bottom: 15px;
  height: 175px;
  opacity: 0;
  position: absolute;
  right: 20px;
  transform: translateY(100%) rotate(5deg);
  width: 175px;
  visibility: hidden;
  z-index: 30;
}

.snap__viewer img {
  bottom: 0;
  left: 0;
  margin: auto;
  position: absolute;
  right: 0;
  top: 0;
}

.snap__viewer.-show {
  opacity: 1;
  transform: translateY(0) rotate(-5deg);
  visibility: visible;
}

.snap__polaroid {
  border: 10px solid #ffffff;
  height: inherit;
  overflow: hidden;
  position: relative;
  width: inherit;
}

.snap__hidden {
  display: none;
}

.snap__btn {
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  background-color: #ffffff;
  border: 0;
  border-radius: 50%;
  height: 50px;
  overflow: hidden;
  position: absolute;
  top: -20px;
  right: -20px;
  width: 50px;
  z-index: 10;
}

.snap__btn:active {
  box-shadow: 0 1px 6px 0 rgba(0, 0, 0, 0.12), 0 1px 6px 0 rgba(0, 0, 0, 0.24);
}

.snap__btn[disabled] {
  background-color: #9e9e9e;
}

.snap__btn svg {
  transition-property: opacity;
  transition-duration: 0.5s;
  transition-timing-function: cubic-bezier(0.19, 1, 0.22, 1);
  fill: #000000;
  height: 25px;
  opacity: 1;
  width: 25px;
}

.snap__btn circle {
  fill: #000000;
}

.snap__btn.-hide {
  transform: translateY(30vh);
}

.snap__btn.-camera {
  transition-property: transform;
  transition-duration: 0.5s;
  transition-timing-function: cubic-bezier(0.19, 1, 0.22, 1);
  bottom: 90px;
  height: 75px;
  left: 0;
  margin: auto;
  padding: 18px;
  position: absolute;
  right: 0;
  top: auto;
  width: 75px;
  z-index: 15;
}

.snap__btn.-camera svg {
  fill: #000000;
  height: 40px;
  width: 40px;
}

.snap__btn.-delete {
  background-color: #f44336;
}

.snap__btn.-delete svg {
  fill: #ffffff;
}

.snap__btn.-delete circle {
  fill: #000000;
}
